<template>
  <div id="app">
    <el-menu theme="dark" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
      <el-menu-item index="1">用户管理</el-menu-item>
      <el-menu-item index="2">设备管理</el-menu-item>
      <el-submenu index="3">
        <template slot="title">权限管理</template>
        <el-menu-item index="3">角色管理</el-menu-item>
        <el-menu-item index="4">呼叫优先管理</el-menu-item>
      </el-submenu>
    </el-menu>
    <div v-if="this.changeNumber === '1'" class="paddingtop20">
      <el-row :gutter="20">
        <el-col :span="4"><div class="u_m_l">
          <ul>
            <li v-for="(item,index) in listObj">
              <el-radio class="radio" v-model="radio[0]" :label="index">{{item.name}}</el-radio>
              <ul v-if="radio[0] === index" class="marginl20">
                <li v-for="(item1,index1) in item.next">
                  <el-radio class="radio" v-model="radio[1]" :label="index + '-' + index1">{{item1.name}}</el-radio>
                  <ul v-if="radio[1] === index + '-' + index1" class="marginl20">
                    <li v-for="(item2,index2) in item1.next">
                      <el-radio class="radio" v-model="radio[2]" :label="index + '-' + index1 + '-' + index2">{{item2.name}}</el-radio>
                      <ul v-if="radio[2] === index + '-' + index1 + '-' + index2" class="marginl20">
                        <li v-for="(item3,index3) in item2.next">
                          <el-radio class="radio" v-model="radio[3]" :label="index + '-' + index1 + '-' + index2 + '-' + index3">{{item3.name}}</el-radio>
                          <ul v-if="radio[3] === index + '-' + index1 + '-' + index2 + '-' + index3" class="marginl20">
                            <li v-for="(item4,index4) in item3.next">
                              <el-radio class="radio" v-model="radio[4]" :label="index + '-' + index1 + '-' + index2 + '-' + index3" + '-' + index4>{{item4.name}}</el-radio>
                            </li>
                          </ul>
                        </li>
                      </ul>
                    </li>
                  </ul>
                </li>
              </ul>
            </li>
          </ul>
        </div></el-col>
        <el-col :span="20"><div class="u_m_r">
          <el-table :data="tableData3" height="600" border style="width: 100%">
            <el-table-column prop="date" label="警号">
            </el-table-column>
            <el-table-column prop="name" label="姓名">
            </el-table-column>
            <el-table-column prop="address" label="单位">
            </el-table-column>
            <el-table-column prop="address" label="职务">
            </el-table-column>
            <el-table-column prop="address" label="手机号">
            </el-table-column>
            <el-table-column prop="address" label="警务通 APP">
            </el-table-column>
            <el-table-column prop="address" label="4g执法仪">
            </el-table-column>
            <el-table-column prop="address" label="4G车载">
            </el-table-column>
            <el-table-column prop="address" label="云镜">
            </el-table-column>
            <el-table-column prop="address" label="摩托车载">
            </el-table-column>
            <el-table-column prop="address" label="其他设备">
            </el-table-column>
            <el-table-column label="操作" width="120px">
              <template scope="scope">
                <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div></el-col>
      </el-row>
    </div>
    <div v-if="this.changeNumber === '2'">
      2
    </div>
    <div v-if="this.changeNumber === '3'">
      3
    </div>
    <div v-if="this.changeNumber === '4'">
      4
    </div>
  </div>
</template>
<script>
import Hello from './components/Hello'

export default {
  name: 'app',
  data(){
    return {
      activeIndex:'1',
      changeNumber:'1',
      radio:[],
      oldRadio:[],
      flag:false,
      listObj:[
        {
          name:'市直局',
          next:[
            {
              name:'警察局',
              next:[
                {
                  name:'第一小队',
                  next:[
                    {
                      name:'sososo'
                    }
                  ]
                },
                {
                  name:'第二小队'
                }
              ]
            }
          ]
        },
        {
          name:'市直局',
          next:[
            {
              name:'警察局',
              next:[
                {
                  name:'第一小队'
                },
                {
                  name:'第二小队'
                }
              ]
            }
          ]
        },
        {
          name:'市直局',
          next:[
            {
              name:'警察局',
              next:[
                {
                  name:'第一小队'
                },
                {
                  name:'第二小队'
                }
              ]
            }
          ]
        }
      ],
      tableData3: [{
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-08',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-06',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-07',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }]
    }
  },
  methods:{
    handleSelect(val){
      this.changeNumber = val;
    },
    sendMsgHandler(stag){
      var str = this.radio[stag].toString();
      var arr = str.split('-');
      var nowObj = this.listObj;
      var argArr = [];
      for(var i = 0;i<arr.length;i++){
        argArr.push(nowObj[arr[i]].name);
        nowObj = nowObj[arr[i]].next;
      }
      console.log(argArr);
    }
  },
  watch:{
    radio(val){
      let n = val.length;
      for(var i = 0;i<val.length;i++){
        if(val[i] !== this.oldRadio[i]){
          n = i;
          this.$set(this.oldRadio,i,val[i]);
          continue;
        }
        this.$set(this.oldRadio,i,val[i]);
      }
      this.radio.length = n + 1;
      this.sendMsgHandler(n);
    }
  },
  components: {
    Hello
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  font-size:12px;
}
ul{
  margin:0;
  padding:0;
  list-style:none;
}
.u_m_l{
  min-height:40px;
  padding:20px;
  border:1px solid #eee;
}
.paddingtop20{
  padding-top:20px;
}
.marginl20{
  margin-left:20px;
}
</style>



